.nav__list {
  margin: 0px;
  padding: 0px;
  text-align: center;
}
.nav__item {
  text-transform: uppercase;
  font-size: 20px;
  line-height: 20px;
  color: var(--basic_black);
  font-weight: 500;
  border-top: 1px solid var(--light_grey);
  padding-top: 22px;
  margin-bottom: 24px;
}
.nav__link[href]:hover,
.nav__link:focus {
  color: rgba(0, 0, 0, 0.6);
}
.nav__link[href]:active {
  color: rgba(0, 0, 0, 0.3);
}
.nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 60px;
  height: 60px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.nav__toggle:active,
.nav__toggle:focus {
  background-color: var(--basic_green);
  outline: none;
}
.nav--closed .nav__toggle::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 18px;
  background: url("../img/menu-open.png") no-repeat;
}
.nav--opened .nav__toggle::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  background: url("../img/menu-close.png") no-repeat;
}
.nav--opened .nav__wrap {
  position: absolute;
  width: 100%;
  top: 66px;
  left: 0;
  z-index: 10;
  background-color: var(--basic_white);
}
.nav--nojs .nav__toggle {
  display: none;
}
.nav--nojs .nav__wrap {
  position: static;
}
.nav--nojs .nav__item {
  display: block;
}
@media (max-width: 767px) {
  .nav--closed .nav__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
  .nav__wrap {
    padding-top: 42px;
  }
  .nav__list {
    display: flex;
    flex-wrap: wrap;
  }
  .nav__link {
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
  }
  .nav__item {
    border: none;
    padding-top: 0px;
  }
  .nav__item--activ {
    padding-bottom: 10px;
    border-bottom: 2px solid var(--basic_green);
  }
  .nav__item:not(:last-child) {
    margin-right: 35px;
  }
  .nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .nav--main .nav__link {
    color: var(--basic_white);
  }
  .nav--main .nav__item--activ {
    border-bottom: 2px solid var(--basic_white);
  }
  .nav--main .nav__link[href]:hover,
  .nav--main .nav__link:focus {
    color: rgba(255, 255, 255, 0.6);
  }
  .nav--main .nav__link:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .nav__link {
    font-size: 20px;
  }
}
.index-info {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 22px;
  min-height: 480px;
}
.index-info::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 305px;
  z-index: -1;
  background: url("../img/index-background-mobile@1x.jpg") no-repeat;
  background-size: cover;
  background-position: top;
}
@media (min-resolution: 2dppx) {
  .index-info::before {
    background: url("../img/index-background-mobile@2x.jpg") no-repeat;
    background-size: cover;
  }
}
.index-info__title {
  font-size: 36px;
  line-height: 36px;
  color: var(--basic_white);
  font-weight: normal;
  text-align: center;
  margin: 0px;
  padding-top: 27px;
  margin-bottom: 25px;
}
.index-info__slogan {
  display: block;
  font-size: 14px;
  line-height: 14px;
  color: var(--basic_white);
  text-transform: uppercase;
  text-align: center;
}
.index-info__img--desktop {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}
.index-info__img--tablet {
  display: none;
}
.index-info__btn {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  display: block;
  width: 280px;
  text-align: center;
  color: var(--basic_white);
  text-transform: uppercase;
  background-color: var(--basic_green);
  line-height: 40px;
  align-self: center;
  margin-top: auto;
}
.index-info__btn:hover,
.index-info__btn:focus {
  background-color: var(--basic_green-2);
}
.index-info__btn:active {
  background-color: var(--basic_green-2);
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .index-info {
    align-items: start;
    padding: 0px 117px;
    min-height: 810px;
    margin-bottom: 0px;
  }
  .index-info__title,
  .index-info__slogan {
    color: var(--basic_black);
  }
  .index-info__title {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    margin: 0px;
    margin-bottom: 40px;
    padding: 0px;
  }
  .index-info__slogan {
    margin-bottom: 53px;
    font-size: 20px;
  }
  .index-info::before {
    display: none;
  }
  .index-info__img--tablet {
    position: absolute;
    bottom: 0px;
    z-index: -1;
    display: block;
    left: 50%;
    transform: translateX(-50%);
  }
  .index-info__img--desktop {
    display: none;
  }
  .index-info__btn {
    margin-top: 0px;
    align-self: flex-start;
    width: 244px;
    font-size: 20px;
    line-height: 54px;
  }
}
@media (min-width: 1440px) {
  .index-info {
    padding-left: 190px;
    min-height: 470px;
  }
  .index-info__img--tablet {
    display: none;
  }
  .index-info__img--desktop {
    display: block;
    width: 552px;
    top: -80px;
    left: 58%;
  }
}
.program {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  padding: 0px 20px;
}
.program__wrap {
  background-color: var(--basic_grey);
  padding: 30px 33px 18px 19px;
}
.program__wrap:not(:last-child) {
  margin-bottom: 20px;
}
.program__title {
  position: relative;
  padding-left: 70px;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: var(--basic_black);
  font-weight: normal;
  margin: 0px;
  margin-bottom: 30px;
}
.program__img--slim {
  left: 0px;
  top: -7px;
  height: 53px;
  width: 50px;
  position: absolute;
}
.program__img--pro {
  left: -7px;
  top: -7px;
  height: 50px;
  width: 67px;
  position: absolute;
}
.program__text {
  font-family: Arial;
  margin: 0px;
  margin-bottom: 43px;
}
.program__link {
  position: relative;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: var(--basic_black);
}
.program__link::after {
  content: "";
  left: 100px;
  height: 50px;
  width: 67px;
  position: absolute;
  background: url("../img/icon_arrow.svg") no-repeat;
}
.program__link:hover::after {
  content: "";
  left: 100px;
  height: 50px;
  width: 67px;
  position: absolute;
  background: url("../img/icon_arrow-long.svg") no-repeat;
}
.program__link:active::after,
.program__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .program {
    margin-bottom: 55px;
    padding: 0px 30px;
  }
  .program__wrap {
    padding: 72px 62px 59px 52px;
  }
  .program__wrap:not(:last-child) {
    margin-bottom: 34px;
  }
  .program__title {
    padding: 0px;
    font-size: 36px;
    margin-bottom: 28px;
  }
  .program__img--slim {
    width: 200px;
    height: 211px;
    left: 400px;
  }
  .program__img--pro {
    width: 268px;
    height: 200px;
    left: 365px;
  }
  .program__link::after,
  .program__link:hover::after {
    left: 133px;
  }
  .program__text {
    width: 55%;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 56px;
  }
  .program__link {
    font-size: 20px;
  }
