}
.features__item:not(:last-child) {
  margin-bottom: 20px;
}
.features__img {
  width: 60px;
  height: 60px;
  margin-right: 20px;
}
.features__text {
  width: 70%;
}
.features__leaf {
  background: url("../img/advantage-leaf@1x.png") no-repeat center;
  background-color: var(--basic_green);
}
.features__powder {
  background: url("../img/advantage-powder@1x.png") no-repeat center;
  background-color: var(--basic_green);
}
.features__dining {
  background: url("../img/advantage-dining@1x.png") no-repeat center;
  background-color: var(--basic_green);
}
.features__clock {
  background: url("../img/advantage-clock@1x.png") no-repeat center;
  background-color: var(--basic_green);
}
@media (min-width: 768px) {
  .features {
    padding: 0 30px;
    padding-bottom: 82px;
    margin: 0px;
    border-bottom: 1px solid var(--light_grey);
  }
  .features__list {
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 113px;
    grid-column-gap: 60px;
  }
  .features__title {
    font-size: 60px;
    margin-bottom: 83px;
  }
  .features__item {
    position: relative;
    font-size: 16px;
    line-height: 24px;
    flex-direction: column;
