  }
  .features__img {
    height: 80px;
    width: 80px;
    margin-bottom: 32px;
  }
  .features__leaf::after,
  .features__clock::after,
  .features__dining::after,
  .features__powder::after {
    position: absolute;
    font-size: 280px;
    color: var(--basic_grey);
    z-index: -1;
    top: 88px;
    left: 120px;
    font-family: "Oswald", "Arial Narrow", sans-serif;
  }
  .features__leaf::after {
    content: "1";
  }
  .features__powder::after {
    content: "2";
  }
  .features__dining::after {
    content: "3";
  }
  .features__clock::after {
    content: "4";
  }
}
@media (min-width: 1440px) {
  .features {
    padding: 0 110px;
    margin-bottom: 70px;
    border: none;
  }
  .features__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 94px;
  }
  .features__item:not(:last-child) {
    margin-bottom: 0px;
  }
  .features__text {
    width: auto;
  }
}
