  left: 0px;
  border: 1px solid var(--special_grey);
  cursor: pointer;
}
.order__checkbox:checked + .order__checkbox--label::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 30px;
  top: -3px;
  left: 0px;
  border: 1px solid var(--special_grey);
  background: url("../img/tick.svg") no-repeat center;
}
.order__checkbox:hover + .order__checkbox--label::before,
.order__checkbox:focus + .order__checkbox--label::before {
  border: 1px solid var(--dark_grey);
}
.order__checkbox:disabled + .order__checkbox--label::before,
.order__checkbox:disabled + .order__checkbox--label::after,
.order__checkbox:disabled + .order__checkbox--label {
  opacity: 0.3;
}
.order__textarea {
  width: 100%;
  padding: 13px;
}
.order__btn {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  width: 100%;
  text-align: center;
  color: var(--basic_white);
  text-transform: uppercase;
  background-color: var(--basic_green);
  line-height: 40px;
  font-size: 16px;
  border: none;
  margin-bottom: 23px;
  cursor: pointer;
}
.order__btn:hover,
.order__btn:focus {
  background-color: var(--basic_green-2);
}
.order__btn:active {
  background-color: var(--basic_green-2);
  color: rgba(255, 255, 255, 0.3);
}
.order__info {
  display: block;
  font-family: Arial;
  text-align: center;
}
